import { s as supabase } from "../../../chunks/supabaseClient.js";
async function load() {
  const allClassroomMapData = (await supabase.from("classroomMap").select("*").order("day")).data;
  const tagDays = allClassroomMapData.filter(({ tags }) => tags.length > 0);
  return { tagDays };
}
export {
  load
};
